import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {EducationList} from './page/EducationList'
import {AddEducation} from './page/AddEducation'
import {EditEducation} from './page/EditEducation'
import EducationScreens from './Screens'

const EducationRoutes: React.FC = () => {
  return (
    <Switch>
      <Route path={EducationScreens.EDUCATION_LIST.PATH} component={EducationList} />
      <Route path={EducationScreens.ADD_EDUCATION.PATH} component={AddEducation} />
      <Route path={EducationScreens.EDIT_EDUCATION.PATH} component={EditEducation} />
      <Redirect from='/education' exact={true} to={EducationScreens.EDUCATION_LIST.PATH} />
      <Redirect to={EducationScreens.EDUCATION_LIST.PATH} />
    </Switch>
  )
}

export default EducationRoutes
