import axios from 'axios'
import {BaseResponse2, BaseResponse, Pagination2} from 'src/app/models/api.types'
import {Education} from '../models/Education'

export const url = `/api/v1/education`

type EducationListParam = {
  page: number
  limit: number
  search?: string
  statuses?: string[]
  // role_id?: string
  // cancelToken?: any
}

export function createEducation(description: string, status: number) {
  return axios.post(url, {description: description, status: status})
}
export function updateEducation(id: string, description: string, status: number) {
  return axios.put(url + `/${id}`, {description: description, status: status})
}

export function deleteEducation(id: string) {
  return axios.delete(url + `/${id}`)
}

export function getEducationDetail(id: string) {
  return axios.get<BaseResponse<Education>>(url + `/${id}`)
}

export function getEducationList({
  page,
  limit,
  search,
  statuses,
}: // role_id,
// cancelToken,
EducationListParam) {
  return axios.get<BaseResponse2<Pagination2<Education>>>(url, {
    params: {
      page,
      limit,
      search,
      // role_id,
      statuses,
    },
    // cancelToken,
  })
}
