/* eslint-disable array-callback-return */
/* eslint-disable react-hooks/exhaustive-deps */
import {useFormik} from 'formik'
import React, {useEffect, useMemo, useState} from 'react'
import {useHistory} from 'react-router'
import {PageTitle} from 'src/_metronic/layout/core'
import {getTitle} from 'src/app/utils/title-utils'
// import {RolePlatform} from '../../role-management/models/Role'
import AlertSuccess from 'src/app/components/AlertSuccess'
import EducationScreens from '../Screens'
import {useParams} from 'react-router-dom'
import Select from 'react-select'
import {ReactSelectMetronicTheme} from 'src/app/components/CustomReactSelect'
import {SelectComponentsConfig} from 'react-select'
import {getEducationDetail, updateEducation} from '../redux/EducationCRUD'

const initialValues = {
  education: '',
  status: '',
}


export const EditEducation = () => {
  // States
  const [education, setEducation] = useState('')
  const [success, setSuccess] = useState('')
  const [selectedStatus, setSelectedStatus] = useState<{label: string; value: number} | undefined>()
  const statList = [
    {label: 'active', value: 1},
    {label: 'inactive', value: 0},
  ]
  // Variables
  const {id} = useParams<any>()
  const history = useHistory()
  // const {data}: any = useSelector<RootState>(({admin}) => admin, shallowEqual)
  const formik = useFormik({
    initialValues,
    validateOnBlur: false,
    validateOnChange: false,
    onSubmit: (values: any, {setStatus, setSubmitting}) => {
      setSubmitting(true)
      setTimeout(() => {
        updateEducation(id, education, selectedStatus?.value ?? values.status)
          .then(() => {
            // dispatch(EducationRedux.actions.setSuccess('Data berhasil disimpan.'))
            history.goBack()
          })
          .catch(() => {
            setSubmitting(false)
            setStatus('Update Admin gagal.')
          })
      }, 1000)
    },
  })
  useEffect(() => {
    const getData = async () => {
      try {
        const detail = await getEducationDetail(id)
        setEducation(detail.data.data?.description ?? '')
        detail.data.data?.status === 0
          ? setSelectedStatus({label: 'inactive', value: detail.data.data?.status})
          : setSelectedStatus({label: 'active', value: detail.data.data?.status ?? 1})

        formik.setFieldValue('education', detail.data.data?.description)
        formik.setFieldValue('status', selectedStatus)
      } catch (error) {}
    }

    getData()
  }, [id])
  //start::TITLE_FUNC
  const pageTitle = useMemo(() => EducationScreens.ADD_EDUCATION.TITLE, [])

  const breadcrumbs = useMemo(
    () => [
      {
        isActive: false,
        path: EducationScreens.EDUCATION_LIST.PATH,
        title: EducationScreens.EDUCATION_LIST.TITLE,
      },
      {isActive: false, path: '', title: '', isSeparator: true},
    ],
    []
  )

  useEffect(() => {
    document.title = getTitle(pageTitle)
  }, [])
  //end::TITLE_FUNC

  return (
    <div className='card'>
      <PageTitle breadcrumbs={breadcrumbs}>{pageTitle}</PageTitle>
      <div className='card-body'>
        <AlertSuccess message={success} handleClose={() => setSuccess('')} />
        {/* <ModalSent
          show={sentData !== undefined}
          data={sentData}
          handleClose={() => setSentData(undefined)}
  />*/}
        <form className='form w-100' onSubmit={formik.handleSubmit} noValidate>
          <div className='mb-10 row'>
            <div className='col-12 col-md-6 mb-10 mb-md-0'>
              <label className='flex-fill form-label fs-6 fw-bolder text-gray-800'>Education</label>
              <input
                {...formik.getFieldProps('education')}
                type='text'
                className='form-control form-control-lg form-control-solid'
                onChange={(value: any) => {
                  formik.setFieldValue('education', value.target.value)
                  setEducation(value.target.value)
                }}
              />
            </div>
            <div className='col-12 col-md-6'>
              <label className='flex-fill form-label fs-6 fw-bolder text-gray-800'>Status*</label>
              <Select
                placeholder='All Status'
                cacheOptions
                defaultOptions
                components={ReactSelectMetronicTheme as SelectComponentsConfig<any, true>}
                value={selectedStatus}
                options={statList}
                onChange={(value: any) => {
                  setSelectedStatus(value)
                  formik.setFieldValue('status', value.value)
                }}
                tabIndex='5'
              />
            </div>
          </div>

          <div className='border-top pt-10 cardfooter-btns'>
            <button
              type='button'
              className={`btn btn-lg btn-light fw-bolder me-4`}
              onClick={() => history.goBack()}
              tabIndex={8}
            >
              Cancel
            </button>
            <button
              type='submit'
              className='btn btn-lg btn-secondary fw-bolder me-4'
              disabled={formik.isSubmitting}
              tabIndex={9}
            >
              <span className='indicator-label'>Save</span>
              {formik.isSubmitting && (
                <span className='indicator-progress'>
                  Mohon Tunggu...
                  <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
